.info-card {
  width: 15rem;
  height: 18.3rem;
  background-color: white;
  box-shadow: 0 0 15px #8d8d8d;
  display: flex;
  flex-direction: column;
  border-radius: 7px;
  transition: all 0.2s ease-in-out;
}

.info-card:hover {
  box-shadow: 0 0 18px #777777;
}

.info-card__icon {
  width: 100%;
  height: 35%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.info-card__icon .fa-icon {
  font-size: 4rem;
  margin: auto;
  /* margin-left: auto;
  margin-right: auto;
  margin-top: auto; */
}

.info-card__title {
  padding: 0.5rem 0;
  width: 100%;
  text-align: center;
  color: white;
  background-color: #3F9CD6;
  margin-top: 0;
  margin-bottom: 0rem;
}

.info-card_secondary .info-card__title{
  color: #3F9CD6;
  background-color: white;
}

.info-card__text-container {
  width: 100%;
  height: 55%;
  background-color: white;
  display: flex;
  border-radius: 0 0 7px 7px;
}

.info-card_secondary .info-card__text-container {
  background-color: #3F9CD6;
}

.info-card__text {
  margin: auto;
  text-align: center;
  padding: 5%;
  color: #3F9CD6;
}

.info-card_secondary .info-card__text {
  color: white;
}
